.footer {
  background-color: #333;
  width: 100%;
}

.footer__section{
  max-width: var(--max-width);
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  color: #fff;
}

.footer__logo {
  padding: 5rem;
  align-self: center;
}
.footer__logo-img {
  width: 15rem;
}

.footer__content {
  display: flex;
  justify-content: space-between;
  padding-top: 2rem;
  padding-bottom: 5rem;
}

.footer__list {
  list-style: none;
}

.footer__list-title {
  font-size: 1.6rem;
  font-weight: 600;
  padding-bottom: 1rem;
  /* padding-left: 5px; */
}

.footer__list-item {
  margin-top: 0.5rem;
  font-size: 1.2rem;
  transition: all 0.3s;
  padding: 0.2rem 0.3rem;
}

.footer__list-item:hover {
  cursor: pointer;
  background-color: var(--primary-color);
  border-radius: 0.5rem;
  filter: brightness(1.5);
}

/* copyright footer */

.footer__bottom {
  background-color: black;
  padding: 1rem;
  text-align: center;
  color: #fff;
  font-size: 1.3rem;
}

.footer__bottom span {
  padding: 0 0.4rem;
}
.footer__bottom_links,
.footer__bottom a {
  text-decoration: none;
  color: #fff;
  transition: all 0.3s;
}
.footer__bottom a:hover {
  color: var(--primary-color);
}

/* Social links and privacy policy */
.footer__social {
  display: flex;
  padding: 3rem 0rem;
  justify-content: space-between;
}

.footer__social-logo{
  display: flex;
}

.footer__social-heading{
  text-transform: uppercase;
  font-size: 1.4rem;
}

.footer__privacy {
  display: flex;
}
.privacy__item{
  margin-left: 2rem;
}


/* media query */
@media only screen and (max-width: 600px) {
  .footer__section{
    width: 100%;
    padding: 2rem;
  }
  .footer__logo{
    padding: 3rem;
  }
  .footer__content{
    gap: 5rem;
    flex-wrap: wrap;
  }
  .footer__list-title{
    font-size: 2rem;
  }
  .footer__social{
    flex-wrap: wrap;
    gap: 4rem;
  }
  .footer__list-item {
    font-size: 1.5rem;
  }
  .footer__bottom {
  margin-bottom: 7rem;
}
}