.category_container {
  height: 17rem;
  display: flex;
  justify-content: space-between;
}

.left_side {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.heading {
  font-size: 2.2rem;
  font-weight: 500;
  padding: 1rem;
}

.text_btn {
  padding: 2rem 0 1rem 1rem;
  font-size: 2rem;
  color: var(--primary-color);
  display: flex;
  align-items: center;
}
.rightArrow {
  padding-left: 0.6rem;
  height: 2.2rem;
}

.image_container {
  padding: 1rem 0;
  background-color: #fff;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

@media only screen and (max-width: 600px) {
  .category_container {
    height: 14rem;
  }
  .heading {
  font-size: 1.8rem;
  padding: 0.5rem;
}
.text_btn {
  padding: 1rem 0 1rem 0.5rem;
  font-size: 1.5rem;
}
}
