.arrow__container {
  position: relative;
  overflow: hidden;
}
.arrow__icon {
  height: 5rem;
  top: 50%;
  position: absolute;
  transform: translateY(-50%);
  z-index: 10;
  fill: var(--primary-color) ;
  /* box-shadow: rgba(207, 204, 204, 0.2) 0px 8px 24px; */
  cursor: pointer;
  display: none;
}

.arrow__icon-left {
  left: -1.5%;
}
.arrow__icon-right {
  right: -1.5%;
}

/* media query */

@media only screen and (max-width:600px) {
  .arrow__icon{
    display: none;
  }
}