.security__container{
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.security__item{
    background-color: #fff;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 2rem;
}

.security__icons{
    stroke: rgb(39, 38, 38);
    height: 6rem;
}

.security__heading{
    font-size: 2rem;
    padding: 2rem 0;
}

.security__para{
    font-size: 1.4rem;
}


/* media query */
@media only screen and (max-width: 600px){
    .security__container{
    flex-direction: column;
    row-gap: 2rem;

}
}