.social__icons {
  height: 2.5rem;
  width: 2.5rem;
  margin-left: 3rem;
}

.social__icons:hover {
  cursor: pointer;
}

.facebook:hover {
  fill: #1877f2;
}

.instagram:hover {
  fill: #e4405f;
}

.twitter:hover {
  fill: #1da1f2;
}

.youtube:hover {
  fill: #cd201f;
}

@media only screen and (max-width: 600px) {
  .facebook {
    fill: #1877f2;
  }

  .instagram {
    fill: #e4405f;
  }

  .twitter {
    fill: #1da1f2;
  }

  .youtube {
    fill: #cd201f;
  }
}
