/* 1rem = 10px */
.header__center {
  width: 100%;
  background-color: #f8f9fa;
  box-shadow: var(--default-box-shadow);
  /* border-bottom: 0.5px solid var(--default-text-color); */
  display: flex;
  flex-direction: column;
}

.header_container {
  width: 100%;
  max-width: var(--max-width);
  height: 8rem;
  align-self: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 1rem;
}

.header__logo {
  /* padding-left: 2.5rem; */
  cursor: pointer;
}
.header__logo_img {
  width: 10rem;
}

.search {
  display: flex;
  flex: 0 0 40%;
  height: 50%;
  align-self: center;
}

.search__input {
  width: 90%;
  border-radius: 10rem;
  background-color: transparent;
  transition: all 0.2s;
  margin-right: -4.5rem;
  padding: 1rem 2rem;
  border: 0.5px solid var(--default-text-color);
  font-size: inherit;
  color: inherit;
  font-family: inherit;
  transition: all 0.3s;
}
.search__input:focus {
  outline: none;
  width: 100%;
  /* background-color: #f7f9fa; */
  border: 0.5px solid var(--default-text-color);
}

.search__input::-webkit-input-placeholder {
  font-weight: 100;
  color: #a4a8af;
}
.search__button {
  height: 80%;
  margin-right: 3rem;
  align-self: center;
  background-color: transparent;
  border: none;
}

.header__icon {
  height: 2rem;
}

.search__icon {
  padding: 0 1.2rem;
  stroke: currentColor;
}

.header__right {
  display: flex;
  justify-items: center;
  align-items: center;
  font-size: 1.6rem;
}

/* user and cart icons */
.login {
  display: flex;
  padding-right: 2rem;
  margin-right: 4rem;
  align-items: center;
  justify-items: center;
  cursor: pointer;
}

.user_icon {
  height: 2.5rem;
}

.user_text {
  padding-left: 1rem;
}

.cart {
  display: flex;
  align-items: center;
  justify-items: center;
  cursor: pointer;
  position: relative;
}

.cart_icon {
  height: 2.5rem;
}
/* .cart__icon-container{
  position: relative;
} */

.cart__quantity {
  position: absolute;
  display: inline-block;
  left: 2rem;
  bottom: 1.8rem;
  font-size: 1rem;
  padding: 0.2rem 0.5rem;
  background: var(--primary-color);
  border-radius: 50%;
  color: #fff;
}
.cart_text {
  padding-left: 1rem;
  cursor: pointer;
}

div {
  user-select: none;
}

@media only screen and (max-width: 600px) {
  .search {
    display: none;
  }
  .header_container {
    padding: 2rem;
    height: 6rem;
  }
  .header__logo_img {
    width: 8rem;
  }
  .login{
    margin-right: 1rem;
  }
}
