.container {
  position: absolute;
  top: 5rem;
  left: 0;
  z-index: 100;
  height: 60vh;
  width: 100%;
  background: var(--default-bg);
  border-radius: 1rem;
  box-shadow: var(--default-box-shadow);
}

.details {
  height: 100%;
}

.details__list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 4rem;
  row-gap: 2rem;
}

.list {
  list-style: none;
}
.list__category{
  font-weight: 500;
  font-size: 1.7rem;
  color: var(--primary-color);
}
.list__brand {
  margin-top: 1.2rem;
  font-size: 1.4rem;
  color: var(--default-text-color);
}

.list__item:hover{
  color: var(--primary-color);
}