.card{
    box-shadow: rgba(100, 100, 111, 0.2) 0px 0.7rem 2.9rem 0px;
    transition: all 0.3s;
    border-radius: 1rem;
    overflow: hidden;
}

.card:hover{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 0.5rem 1.5rem;
    cursor: pointer;
}

@media (hover:none){
    .card:hover{
        cursor: none;
    }
}
