.product__quantity-control {
  width: 10rem;
  display: flex;
  justify-content: space-around;
  border: 1px dotted #7f8c8d;
  padding: 0 0.5rem;
}

.decrease {
  display: flex;
  align-items: center;
}

.decrease__button {
  width: 1.5rem;
  height: 0.3rem;
  background-color: var(--default-text-color);
  display: inline-block;
}

.quantity-number {
  font-size: 1.6rem;
  align-self: center;
}

.increase {
  display: flex;
  align-items: center;
}

.increase__button {
  width: 1.5rem;
  height: 0.3rem;
  background-color: var(--default-text-color);
  display: inline-block;
  position: relative;
}

.increase__button::after {
  content: "";
  position: absolute;
  width: 1.5rem;
  height: 0.3rem;
  background-color: var(--default-text-color);
  display: inline-block;
  transform: rotate(90deg);
}
