.container{
    display: flex;
    height: 100%;
}

.left{
    padding-top: 2rem;
    flex-basis: 25%;
    background-color: #074c790f;
}
.right{
    padding: 2rem;
    width: 75%;
}

.category__name{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem .5rem 0.5rem 1.5rem;
    align-items: center;
    font-size: 1.8rem;
    color: var(--default-text-color);
}

 .active{
    background-color: var(--default-bg);
    color: var(--primary-color);
}

.icon{
    height: 2rem;
    transform: rotate(-90deg);
}