
.line{
  height: 0.2rem;
  width: 2rem;
  background-color: var(--default-text-color);
  display: inline-block;
  position: relative;
  margin-right: 1rem;
}

.line::before{
  height: 0.2rem;
  width: 2rem;
  content: "";
  position: absolute;
  top: -1.2rem;
  background-color: var(--default-text-color);
}
.line::after{
  height: 0.2rem;
  width: 2rem;
  content: "";
  position: absolute;
  top: -.6rem;
  background-color: var(--default-text-color);
}