.polices__container {
  display: flex;
  width: 96rem;
}
.polices__nav {
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  row-gap: 2rem;
  flex-basis: 36rem;
}
.polices__nav--item {
  font-size: 1.8rem;
  transition: all 0.3s;
  color: var(--primary-color);
  padding: 0.2rem 0.6rem;
}

.polices__nav--item:hover {
  cursor: pointer;
  background-color: var(--primary-color);
  color: #fff;
  border-radius: 0.5rem;
}
.active {
  background-color: var(--primary-color);
  color: #fff;
  padding: 0.2rem 0.6rem;
  border-radius: 0.5rem;
}

.polices__details {
  flex-basis: 60rem;
}

.privacy__heading {
  font-size: 4rem;
  font-weight: 600;
}
.privacy__content--heading {
  font-size: 1.8rem;
  font-weight: 400;
  padding-top: 2rem;
}
.privacy__content--heading-1 {
  font-size: 2rem;
  font-weight: 500;
  padding-top: 2rem;
}
.privacy__content--para {
  font-size: 1.4rem;
  padding-top: 1rem;
}
.privacy__links {
  color: rgb(228, 48, 17);
}
.privacy__links:hover {
  color: var(--primary-color);
}

@media only screen and (max-width: 600px) {
  .polices__container {
    display: flex;
    width: auto;
    flex-direction: column;
  }
  .polices__nav {
  display: flex;
  flex-basis: 16rem;
}
}
