:root {
  --primary-color: #074c79;
  /* --default-text-color: #7F8C8D; */
  --default-text-color: #25292b;
  --max-width: 127.5rem;
  --default-box-shadow: rgb(148 148 148 / 30%) 0 0.2rem 0.6rem;
  --bottom-border:1px solid #d0d0d0;
  --default-bg: #f8f9fa;
  --mobile-width: 37.5em;
}
a {
  text-decoration: none;
}

/* a:hover {
  color: var(--primary-color);
} */

.main__content{
    width: var(--max-width) !important;
    display: flex;
    align-self: center;
    flex-direction: column;
    align-items: center;
    margin: 2rem 0;
}

.footer__content{
    background-color: #333;
    align-self: center;
    width: 100%;
    align-items: center;
}

/* css code for meadia query */
@media only screen and (max-width: 600px) {
  .main__content{
    width: 100% !important;
    margin: 0.5rem 0;
    padding: 1rem;
  }
}