.slides__container {
  position: relative;
}
.arrow__icon {
  height: 5rem;
  top: 50%;
  transform: translate(0, -50%);
  position: absolute;
  z-index: 10;
  fill: var(--default-text-color);
  box-shadow: rgba(3, 3, 3, 0.2) 0px 0.8rem 2.4rem;
  cursor: pointer;
}

.arrow__icon-left {
  left: 0.5%;
}
.arrow__icon-right {
  right: 0.5%;
}

/* bootom dots for slides */
.dots {
  position: absolute;
  left: 50%;
  bottom: 2%;
  transform: translate(-50%, 0);
  display: flex;
  column-gap: 2rem;
  z-index: 10;
}

.dot {
  height: 1rem;
  width: 1rem;
  z-index: 10;
  background-color: var(--default-text-color);
  cursor: pointer;
}

.dot__seletct {
  background-color: #fff;
  transition: all ease-in-out 0.4s;
}
.dot__deseletct {
  background-color: var(--default-text-color);
}
