.innerContainer {
}
.container__detail {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  padding: 1.5rem;
  box-shadow: var(--default-box-shadow);
}

.container__heading {
  font-size: 2.2rem;
}

.btn {
  outline: none;
  border: 1px solid transparent;
  padding: 1rem 2rem;
  background-color: var(--primary-color);
  color: #fff;
  font-size: 1.4rem;
  font-weight: 500;
  border-radius: 1rem;
  transition: all 0.3s;
}

.btn:hover{
    cursor: pointer;
    background-color: #fff;
    transition: all 0.3s;
    color: #7F8C8D;
    border: 1px solid var(--primary-color);
}

.container__items {
  padding: 3rem 0;
}

@media only screen and (max-width:600px) {
  .container__items {
  padding: 2rem 0;
}
}
