.nav__center {
  width: 100%;
  background-color: #f8f9fa;
  box-shadow: var(--default-box-shadow);
  display: flex;
  flex-direction: column;
}
.dropdown {
  position: relative;
}
.nav {
  width: 100%;
  max-width: var(--max-width) !important;
  align-self: center;
  display: flex;
  height: 5rem;
  font-size: 1.8rem;
  justify-content: space-between;
  position: relative;
}

.shopby,
.nav__item {
  cursor: pointer;
  border-bottom: 3px solid transparent;
  transition: all 0.2s;
  padding: 1rem 0.3rem;
}

.nav__item {
  display: flex;
}

.nav__active {
  color: var(--primary-color);
  border-bottom: 3px solid var(--primary-color);
}

.nav__down {
  height: 2rem;
  width: 2rem;
  margin-left: 1rem;
  align-self: center;
}

div {
  user-select: none;
}

.fixnav {
  position: fixed;
  z-index: 20;
  top: 0;
}
.none {
}


/* media query */
@media only screen and (max-width: 600px){
  .nav{
    display: none;
  }
}