.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  row-gap: 2rem;
  font-size: 1.5rem;
}

.product__summary {
  display: flex;
  justify-content: space-between;
}

.product__summary-left {
  flex: 1 1 0;
}
.product__imgContainer {
  overflow: hidden;
}

.product__content {
  display: flex;
  flex-direction: column;
  flex: 1 1 0;
  row-gap: 1.5rem;
}

.product__heading {
  font-size: 2.2rem;
}

.product__brand {
  color: var(--primary-color);
}

.product__off {
  padding: 0.2rem 0.7rem;
  background-color: var(--primary-color);
  color: #fff;
  font-weight: 500;
  align-self: flex-start;
}

.product__prices {
  display: flex;
  column-gap: 2rem;
  align-items: center;
}

.product__current-price {
  font-size: 2.3rem;
  font-weight: 800;
}
.product__old-price {
  text-decoration: line-through;
}

.product__add-cart {
  display: flex;
  column-gap: 3rem;
  margin-top: 1.5rem;
}

.product__quantity-control {
  width: 10rem;
  display: flex;
  justify-content: space-around;
  border: 1px dotted #7f8c8d;
  padding: 0 0.5rem;
}

.decrease {
  display: flex;
  align-items: center;
}

.decrease__button {
  width: 1.5rem;
  height: 0.3rem;
  background-color: var(--default-text-color);
  display: inline-block;
}

.quantity-number {
  font-size: 1.6rem;
  align-self: center;
}

.increase {
  display: flex;
  align-items: center;
}

.increase__button {
  width: 1.5rem;
  height: 0.3rem;
  background-color: var(--default-text-color);
  display: inline-block;
  position: relative;
}

.increase__button::after {
  content: "";
  position: absolute;
  width: 1.5rem;
  height: 0.3rem;
  background-color: var(--default-text-color);
  display: inline-block;
  transform: rotate(90deg);
}

.options-heading {
  font-size: 1.6rem;
  font-weight: 600;
  margin-bottom: -1rem;
}

.options-container {
  display: flex;
  flex-wrap: wrap;
  column-gap: 2rem;
}

.options-span {
  font-weight: 600;
  padding: 0.5rem 1.5rem;
  border: 1px solid var(--primary-color);
  border-radius: 0.6rem;
}

.options__selected {
  background-color: #074b79ad !important;
  color: #fff !important;
}
.options-span:hover {
  background-color: #074b79d3;
  color: #fff;
}
