}

.weight-container {
}

.weight-span {
}

.weight-span:hover {
  background-color: #074b7991;
  color: #fff;
}

.flavour-text {
}

.offer-available {
 font-size: 1.8rem;
}

.offer-list {
  font-size: 1.6rem;
}

.list__icon {
