@media only screen and (max-width: 600px) {
  .container {
    gap: 0;
    font-size: 2rem;
  }
  .product__summary {
    flex-direction: column;
    row-gap: 1rem;
  }
  .product__heading {
  font-size: 2.5rem;
}
.offer-available {
 font-size: 2rem;
}
.product__current-price {
  font-size: 2.6rem;
}
.product__old-price {
  font-size: 2.3rem;
}
  .product__benefits {
    display: none;
  }
  .product__add-cart {
    gap: 0;
    margin: 1rem 0;
    justify-content: space-around;
  }
  .cartBtn {
    font-size: 2rem;
    padding: 1rem;
  }
  .cartIcon {
    height: 2.5rem;
  }
  .product__quantity-control {
    padding: 1rem;
    width: 15rem;
  }
  .decrease__button {
    width: 2rem;
    height: 0.4rem;
  }

  .quantity-number {
    font-size: 2.4rem;
    padding: 0 1rem;
  }

  .increase__button {
    width: 2rem;
    height: 0.4rem;
  }

  .increase__button::after {
    width: 2rem;
    height: 0.4rem;
  }

  .flavour-text, .weight-heading {
  font-size: 2rem;
}
.product__details {
  font-size: 1.7rem;
}
  .stickyCart {
    z-index: 100;
    position: fixed;
    bottom: 0;
    width: 95%;
    background-color: #fff;
    box-shadow: rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px, rgba(17, 17, 26, 0.1) 0px 24px 80px;
    border-radius: 1rem;
    overflow: hidden;
  }
}
