.button__card {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.addToCart{
  display: inline-block;
  height: 2rem;
  margin-right: 1rem;
}

.btn {
  background-color: var(--primary-color);
  text-decoration: none;
  font-size: 1.5rem;
  border: none;
  color: white;
  font-weight: 700;
  letter-spacing: 0.2rem;
  padding: 1rem;
  box-shadow: 0 1.0rem 2rem rgba(black, 0.15);
  border-radius: 1rem;
  cursor: pointer;
  transition: all 0.3s;
}

.btn:hover {
  background-color: #000;
}

@media only screen and (max-width: 400px)  and (min-width: 330px) {
  .button__card {
  justify-content: space-between;
}
.btn{
  font-size: 1.4rem;
}
}