.card {
  height: 42rem;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 26rem;
  padding: 1rem 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 1rem 2rem rgba(rgb(138, 120, 120), 0.15);
  background-color: rgba(255, 255, 255);
}

/* card image */
.card__image {
  width: 100%;
  height: 50%;
}

.card__image-item {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.card__heading {
  font-size: 1.6rem;
  line-height: 1.6;
  height: auto;
  width: 100%;
  margin-top: 1.5rem;
  font-weight: 500;
  min-height: 5rem;
}

/* card price */
.card__price {
  margin: 1rem 2.5rem;
  display: flex;
  justify-items: center;
  align-items: center;
}

.price__current {
  margin-right: 1rem;
  font-weight: 700;
  font-size: 1.8rem;
  margin-right: 2rem;
}
.price__old {
  text-decoration: line-through;
  font-size: 1.2rem;
}

.button__card {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.addToCart {
  display: inline-block;
  height: 2rem;
}

.btn {
  background-color: var(--primary-color);
  text-decoration: none;
  font-size: 1.5rem;
  border: none;
  color: white;
  font-weight: 700;
  letter-spacing: 0.2rem;
  padding: 1rem;
  box-shadow: 0 1rem 2rem rgba(black, 0.15);
  border-radius: 1rem;
  cursor: pointer;
  transition: all 0.3s;
}


.btn:hover {
  /* background-color: rgba(0, 0, 0, 0.781); */
  background-color: #000;
}

/* vegnonveg */
.vegnonveg__conatainer {
  display: inline-block;
  position: absolute;
  top: 5%;
  left: 5%;
}

/* wishlist icon */
.wishlist {
  width: 2.5rem;
  height: 2.5rem;
  display: inline-block;
  position: absolute;
  top: 5%;
  right: 5%;
}

/* Offer off*/
.offer {
  margin-top: 1rem;
  align-self: flex-start;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
}

