@media only screen and (max-width: 450px) and (min-width: 400px) {
  .card__heading {
  font-size: 1.5rem;
  line-height: 1.3;
}
  .card {
    width: 23rem;
  }
   .card__price{
    margin: 1rem 0.5rem;
  }
}


@media only screen and (max-width: 400px)  and (min-width: 330px) {
  .card {
    width: 20rem;
    padding: 1rem 1.5rem;
  }
  .card__price{
    margin: 1rem 0.5rem;
  }
}

@media only screen and (max-width: 330px){
  .card {
    width: 26rem;
    padding: 1rem;
  }
  .card__price{
     margin: 1rem 2.5rem;
  }
}