.container{
    display: flex;
    flex-direction: column;
}

.heading{
    font-size: 2.2rem;
    padding-bottom: 2rem;
}

.items{
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 2rem;
}

/* media query */

@media only screen and (max-width: 600px) {
    .container{
    padding: 0 1rem;
}
 .items{
    display: grid;
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(2, 1fr);
}
}