.container {
  display: flex;
  flex-direction: column;
  padding: 3rem;
  background-color: #fff;
  border-radius: 2rem;
  box-shadow: var(--default-box-shadow);
  margin: 2rem 3rem;
  height: 50rem;
  width: 45rem;
}

.top-slider {
  margin-bottom: 3rem;
}

.imageContainer {
  height: 30rem;
  max-width: 30rem;
  margin: auto;
}

.top-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.bottom-slider {
  display: flex;
  overflow-x: scroll;
}

.bottom-img__container {
  border-top: 1px solid #7f8c8d;
  width: 12rem;
  height: 12rem;
  padding: 1rem;
}
.bottom-img {
  /* width: 100%; */
  height: 100%;
  object-fit: cover;
  display: block;
}

@media only screen and (max-width: 440px) {
  .container {
    margin: 1rem;
    height: 35rem;
    width: 95%;
  }
  .bottom-slider{
    display: none;
  }
}
