.container {
  display: flex;
  justify-content: space-between;
  padding: 2rem 0;
  border-bottom: var(--bottom-border);
}
.details {
  display: flex;
}
.imageContainer {
  height: 10rem;
  width: 10rem;
  overflow: hidden;
}

.img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.icon {
  height: 2.5rem;
  width: 2.5rem;
}

.product__info {
  display: flex;
  margin-left: 2rem;
  flex-direction: column;
  justify-content: space-between;
  row-gap: 1rem;
}

.product__name {
  font-size: 1.6rem;
  font-weight: 500;
}

.price__container {
  display: flex;
  column-gap: 2rem;
  justify-items: center;
  align-items: center;
}

.current-price {
  margin-right: 1rem;
  font-weight: 500;
  font-size: 1.6rem;
}
.mrp-price {
  text-decoration: line-through;
  font-size: 1.2rem;
}

.off {
  font-size: 1.4rem;
  color: var(--primary-color);
}

.actions{
  display: flex;
  column-gap: 2rem;
}
