.cartpage__container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 5rem;
}

.card {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 0.7rem 2.9rem 0px;
  border-radius: 1rem;
  overflow: hidden;
}

.bottom-border {
  border-bottom: var(--bottom-border);
}

.card__inner {
  padding: 1rem;
}

.cartpage__products-table {
  flex-basis: 62%;
}

.heading {
  font-size: 2.5rem;
  color: var(--default-text-color);
  font-weight: 700;
  padding-bottom: 1rem;
  border-bottom: var(--bottom-border);
}

.cartpage__sum-table {
  display: flex;
  flex-direction: column;
  flex-basis: 35%;
  row-gap: 3rem;
}

.sum__heading {
  font-size: 1.8rem;
  font-weight: 600;
  padding: 1rem;
}

.sum__details {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  padding: 1rem;
}

.sum__row {
  display: flex;
  justify-content: space-between;
  justify-items: center;
  align-items: center;
}

.sum__text,
.sum__price {
  font-size: 1.6rem;
  font-weight: 400;
  letter-spacing: 1.3px;
}

.sum__price {
  font-weight: 600;
}

.discount__price {
  color: var(--primary-color);
}

.sum__final {
  padding-right: 1rem;
}

.largeTxtBtn {
  font-size: 2.5rem;
}

@media only screen and (max-width: 600px) {
  .cartpage__container {
    display: flex;
    margin-bottom: 5rem;
    flex-direction: column;
    row-gap: 3rem;
  }
}
