.banner__center {
  padding: 0 0 2rem 0;
  display: flex;
  width: 100%;
  /* height: 100%; */
  flex-direction: column;
  overflow: hidden;
}
.header__banners {
  align-self: center;
  display: flex;
  max-width: var(--max-width);
  height: 100%;
  column-gap: 2rem;
}

.banner__left {
  display: flex;
  overflow: hidden;
  height: 50rem;
  width: 85rem;
}

.banner__right {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  height: 50rem;
  width: 45rem;
  overflow: hidden;
}
.banner__right--1 {
  height: 24rem;
  width: 100%;
}
.banner__right--2 {
  height: 24rem;
  width: 100%;
}

.banner__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  overflow: hidden;
}

.banner__hide {
  opacity: 0;
  position: absolute;
  transition: all ease-in-out 0.4s;
  transform: scale(0.6);
}
.banner__unhide {
  transform: scale(1);
  opacity: 1;
  display: block;
}

@media only screen and (max-width: 600px) {
  .banner__center {
    padding: 0 0 1rem 0;
  }
  .header__banners {
    flex-direction: column;
    row-gap: 2rem;
    width: 100%;
  }

  .banner__left {
    display: flex;
    overflow: hidden;
    height: 25rem;
  }

  .banner__right {
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .banner__right--1 {
    height: 25rem;
  }
  .banner__right--2 {
    height: 25rem;
  }
}
