.container {
  display: flex;
  flex-direction: column;
  width: 55rem;
  padding: 5rem;
  row-gap: 4rem;
}

.heading {
  display: flex;
  font-size: 1.2rem;
  color: var(--primary-color);
  justify-content: space-around;
  padding-bottom: 2rem;
}
.heading__login {
  padding-bottom: 0.5rem;
  border-bottom: 3px solid transparent;
}
.heading__signup {
  border-bottom: 3px solid transparent;
}
.heading__active {
  border-bottom: 3px solid var(--primary-color);
}
.form {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}
/* .form__control:not(:last-child) {
  margin-bottom: 2rem;
} */
.input {
  padding: 1.5rem 2rem;
  width: 90%;
  display: block;
  font-size: 1.5rem;
  font-family: inherit;
  border: none;
  color: inherit;
  border-radius: 3px;
  border-bottom: 3px solid transparent;
}

.input:focus {
  outline: none;
  border-bottom: 3px solid var(--primary-color);
  box-shadow: 0 1rem 2rem rgba(var(--primary-color), 0.1);
}
.input:focus:invalid {
  border-bottom: 3px solid red;
}
.input__label {
  font-size: 1.2rem;
  margin-left: 2rem;
  display: block;
  margin-bottom: 0.7rem;
  font-weight: 700;
  transition: all 0.2s;
  transform: translateY(-8rem);
}

.input:placeholder-shown + .input__label {
  opacity: 0;
  visibility: hidden;
  transform: translateY(-4rem);
}

.login__btn {
  align-self: center;
  width: 50%;
}

@media only screen and (max-width: 600px) {
  .container {
    width: 40rem;
    padding: 1rem;
    row-gap: 2rem;
  }
  .heading {
    padding-bottom: 1rem;
    font-size: 1rem;
  }
  .input {
    width: 100%;
    padding: 1rem;
  }
  .input__label {
    margin-left: 1rem;
    transform: translateY(-7rem);
  }
}

.errorMessage{
  font-size: 1.4rem;
  color: red;
}