.vegnonveg {
  width: 1rem;
  height: 1rem;

  position: absolute;
}
.vegnonveg::after {
  content: "";
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  position: absolute;

  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}

.veg {
  border: 1px solid #42d934;
}

.veg::after {
  background-color: #42d934;
}

.nonveg{
    border: 1px solid #ff1d25;
}

.nonveg::after {
  background-color: #ff1d25;
}
